import {BrowserModule} from '@angular/platform-browser';
import {ErrorHandler, NgModule} from '@angular/core';

import {IonicApp, IonicErrorHandler, IonicModule} from 'ionic-angular';

import {StatusBar} from '@ionic-native/status-bar';
import {SplashScreen} from '@ionic-native/splash-screen';
import {ScreenOrientation} from "@ionic-native/screen-orientation";
import {SQLite} from "@ionic-native/sqlite";
import {IonicStorageModule} from "@ionic/storage";

import {MyApp} from './app.component';

import {UserDataProvider} from '../providers/user-data/user-data';

// ionic pro monitoring
import {Pro} from '@ionic/pro';
import { SettingDataProvider } from '../providers/setting-data/setting-data';

const IonicPro = Pro.init('d6a25364', {
  appVersion: "0.0.1"
});

export class MyErrorHandler implements ErrorHandler {
  handleError(err: any): void {
    IonicPro.monitoring.handleNewError(err);
  }
}

@NgModule({
  declarations: [
    MyApp,
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp, {
      preloadModules: true,
      backButtonText: ''
    }, 
{
  links: [
    { loadChildren: '../pages/catalog/catalog.module.ngfactory#CatalogPageModuleNgFactory', name: 'CatalogPage', segment: 'catalog', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/collection/collection.module.ngfactory#CollectionPageModuleNgFactory', name: 'CollectionPage', segment: 'collection', priority: 'high', defaultHistory: [] },
    { loadChildren: '../pages/edit-intro/edit-intro.module.ngfactory#EditIntroPageModuleNgFactory', name: 'EditIntroPage', segment: 'edit-intro', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/edit-password/edit-password.module.ngfactory#EditPasswordPageModuleNgFactory', name: 'EditPasswordPage', segment: 'edit-password', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/edit-phone/edit-phone.module.ngfactory#EditPhonePageModuleNgFactory', name: 'EditPhonePage', segment: 'edit-phone', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/edit-username/edit-username.module.ngfactory#EditUsernamePageModuleNgFactory', name: 'EditUsernamePage', segment: 'edit-username', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/edit/edit.module.ngfactory#EditPageModuleNgFactory', name: 'EditPage', segment: 'edit', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/exam/exam.module.ngfactory#ExamPageModuleNgFactory', name: 'ExamPage', segment: 'exam', priority: 'high', defaultHistory: [] },
    { loadChildren: '../pages/login/login.module.ngfactory#LoginPageModuleNgFactory', name: 'LoginPage', segment: 'login', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/myself/myself.module.ngfactory#MyselfPageModuleNgFactory', name: 'MyselfPage', segment: 'myself', priority: 'high', defaultHistory: [] },
    { loadChildren: '../pages/problem/problem.module.ngfactory#ProblemPageModuleNgFactory', name: 'ProblemPage', segment: 'problem', priority: 'high', defaultHistory: [] },
    { loadChildren: '../pages/profile/profile.module.ngfactory#ProfilePageModuleNgFactory', name: 'ProfilePage', segment: 'profile', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/question/question.module.ngfactory#QuestionPageModuleNgFactory', name: 'QuestionPage', segment: 'question', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/setting/setting.module.ngfactory#SettingPageModuleNgFactory', name: 'SettingPage', segment: 'setting', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/tabs/tabs.module.ngfactory#TabsPageModuleNgFactory', name: 'TabsPage', segment: 'tabs', priority: 'high', defaultHistory: [] }
  ]
}),
    IonicStorageModule.forRoot({
      name: 'myApp',
      driverOrder: ['sqlite', 'indexeddb', 'websql']
    })
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
  ],
  providers: [
    StatusBar,
    SplashScreen,
    ScreenOrientation,
    SQLite,
    UserDataProvider,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    {provide: ErrorHandler, useClass: MyErrorHandler},
    UserDataProvider,
    SettingDataProvider,
  ]
})

export class AppModule {

}



